.wrapper-border-effect {
  position: relative;
}

.wrapper-border-effect .input-border-effect {
  border: none;
  box-shadow: none;
  border-bottom: 2px solid #DDD;
  padding-left: 0;
  padding-right: 0;
  background-color: transparent;
  border-radius: 2px;
}

.wrapper-border-effect .input-border-effect:focus,
.wrapper-border-effect .input-border-effect:hover {
  box-shadow: none;
}

.wrapper-border-effect .input-border-effect + .span-border-effect-left,
.wrapper-border-effect .input-border-effect + .span-border-effect-center,
.wrapper-border-effect .input-border-effect + .span-border-effect-right {
  position: absolute;
  bottom: 0;
  display: block;
  -webkit-transition: 0.3s ease-in-out;
  -moz-transition: 0.3s ease-in-out;
  -o-transition: 0.3s ease-in-out;
  transition: 0.3s ease-in-out;
  width: 0;
  height: 2px;
  background-color: #139d58;
}

.wrapper-border-effect .input-border-effect + .span-border-effect-left {
  left: 0;
}

.wrapper-border-effect .input-border-effect + .span-border-effect-center {
  left: 50%;
}

.wrapper-border-effect .input-border-effect + .span-border-effect-right {
  right: 0;
}

.wrapper-border-effect .input-border-effect:focus + .span-border-effect-left,
.wrapper-border-effect .input-border-effect:focus + .span-border-effect-center,
.wrapper-border-effect .input-border-effect:focus + .span-border-effect-right {
  width: 100%;
}

.wrapper-border-effect .input-border-effect:focus + .span-border-effect-center {
  left: 0;
}

